import React, {Component} from 'react';
import PropTypes from 'prop-types';

class AddNinja extends Component {
  state = {
    ninja: {
      name: '',
      rank: ''
    }
  }

  changeName = (e) => {
    this.setState({
      ninja: {
        name: e.target.value,
        rank: this.state.ninja.rank
      }
    });
  }

  changeRank = (e) => {
    this.setState({
      ninja: {
        rank: e.target.value,
        name: this.state.ninja.name
      }
    });
  }

  addNinja = (e) => {
    e.preventDefault();
    this.props.addNinja(this.state.ninja);
    this.setState({
      ninja: {
        name: '',
        rank: ''
      }
    });
  }

  render() {
    return (
      <div>
        <h2 style={{margin: '10px 10px 2px 10px'}}>Add Ninja Here</h2>
        <form style={ninjaStyle} onSubmit={this.addNinja}>
          <label style={{margin: '5px'}}>Name: </label>
          <input type="text" 
            name="name"
            value={this.state.ninja.name}
            onChange={this.changeName} 
          />
          <label style={{margin: '5px 5px 5px 20px'}} >Rank: </label>
          <input type="text" 
            name="rank"
            value={this.state.ninja.rank}
            onChange={this.changeRank} 
          />
          
          <input type="submit"
            value="Add Ninja"
            style={{margin: '5px 5px 5px 20px'}}
          />
        </form>
      </div>
    )
  }
}

AddNinja.propTypes = {
  addNinja: PropTypes.func.isRequired
}

const ninjaStyle = {
  border: 'groove',
  margin: '5px 10px 20px 10px',
  padding: '10px'
}


export default AddNinja;
