import React, { Component } from 'react';
import './App.css';
import NinjaList from './components/NinjaList';
import Header from './layout/Header';
import AddNinja from './components/AddNinja';
import axios from 'axios';

class App extends Component {
  state = {
    ninjas: []
  }

  hire = (_id) => {
    var ninja = this.state.ninjas.find(ninja => ninja._id === _id);
    var avai = ! ninja.available;
    ninja = {
      available: avai
    }

    axios.put(`http://localhost:8000/api/ninjas/${_id}`, ninja)
      .then(res => {
        if (res){
          this.setState({ninjas: this.state.ninjas.map(function(ninja){
            if (ninja._id === _id) ninja.available = !ninja.available;
            return ninja;
          })});
        }
      });
  }

  componentDidMount() {
    axios.get('http://localhost:8000/api/ninjas')
      .then(res => this.setState({ninjas: res.data}));
  }

  addNinja = (ninja) => {
    axios.post('http://localhost:8000/api/ninjas', ninja)
      .then(res => {
        if (res) 
          this.setState({ninjas: [...this.state.ninjas, res.data]});
      });
    
  }

  deleteNinja = (_id) => {
    axios.delete(`http://localhost:8000/api/ninjas/${_id}`)
      .then(res => {
        if (res){
          this.setState({ninjas: this.state.ninjas.filter(function(ninja){
            return ninja._id !== _id;
          })});
        }
      });
  }

  render() {
    return (
      <div className="App">
        <Header />
        <AddNinja addNinja={this.addNinja}/>
        <NinjaList 
          ninjas={this.state.ninjas} 
          hire={this.hire}
          deleteNinja={this.deleteNinja}
        />
      </div>
    );
  }
}

export default App;
