import React, {Component} from 'react';
import Ninja from './Ninja';
import PropTypes from 'prop-types';

export class NinjaList extends Component {
  render() {
    return this.props.ninjas.map((ninja) => (
      <Ninja 
        key={ninja._id}
        ninja={ninja}
        hire={this.props.hire}
        deleteNinja={this.props.deleteNinja}
      />
    ));
  }
}

NinjaList.propTypes = {
  ninjas: PropTypes.array.isRequired,
  hire: PropTypes.func.isRequired,
  deleteNinja: PropTypes.func.isRequired,
}

export default NinjaList;
