import React, {Component} from 'react';
import PropTypes from 'prop-types';

export class Ninja extends Component {
  isOnline = () => {
    if (this.props.ninja.available){
      return (
        <p style={avaiStyle}>Available</p>
      )
    } else {
      return (
        <p style={busStyle}>Busy</p>
      )
    }    
  }

  hire = () => {
    this.props.hire(this.props.ninja._id);
  }

  btnName = () => {
    if (this.props.ninja.available) return 'Hire';
    else return 'Unhire';
  }

  deleteNinja = () => {
    this.props.deleteNinja(this.props.ninja._id);
  }

  render() {
    return (
      <div style={ninjaStyle}>
        <button onClick={this.deleteNinja} style={btnStyle}>delete</button>
        <button onClick={this.hire} style={btnStyle}>{this.btnName()}</button>
        <h3 style={nameStyle}>{this.props.ninja.name}</h3>
        <p style={rankStyle}>Rank: {this.props.ninja.rank}</p>
        {this.isOnline()}
      </div>
    );
  }
}

Ninja.propTypes = {
  ninja: PropTypes.object.isRequired,
  hire: PropTypes.func.isRequired,
  deleteNinja: PropTypes.func.isRequired,
}

const ninjaStyle = {
  border: 'groove',
  margin: '10px'
}

const nameStyle = {
  margin: '7px 10px'
}

const rankStyle = {
  margin: '5px 20px'
}

const avaiStyle = {
  margin: '5px 20px',
  color: 'green'
}

const busStyle = {
  margin: '5px 20px',
  color: 'grey'
}

const btnStyle = {
  float: 'right',
  overflow: 'auto',
  margin: '10px'
}

export default Ninja;
